@charset "UTF-8";

$MQMobile: 769px;

@charset "UTF-8";

@mixin size($width: 100%, $height: 100%) {
  width: $width;
  height: $height;
}

@mixin flex($row: row, $justify-content: flex-start, $align-items: flex-start) {
  display: flex;
  flex-flow: $row nowrap;
  justify-content: $justify-content;
  align-items: $align-items;
}

@mixin bg-img($url) {
  background-image: url($url);
  background-repeat: no-repeat;
  background-position: center;
  background-size: 100% 100%;
}

@mixin ellipsis() {
  overflow: hidden;
  -ms-text-overflow: ellipsis;
  text-overflow: ellipsis;
  white-space: nowrap;
}

@mixin absolute($top: 0, $right: unset, $bottom: unset, $left: 0) {
  position: absolute;
  top: $top;
  left: $left;
  bottom: $bottom;
  right: $right;
}

@charset "UTF-8";

.wrapper {
  @include size;
  position: relative;
  @include flex(column, flex-start, flex-start);

  .header {
    padding: 0 254px;
    background: #ffffff;
    @include size(100%, 92px);
    box-shadow: 4px 4px 20px rgba(124, 124, 124, 0.25);
    @include flex(row, space-between, center);

    .logo {
      @include size(205px, 32px);
    }

    .state {
      font-family: "Roboto", sans-serif;
      font-size: 16px;
      line-height: 19px;
      color: #e98f39;
      @include flex(row, flex-start, center);

      .indicator {
        display: inline-block;
        @include size(13px, 13px);
        background: #61ff00;
        border-radius: 50%;
        margin-right: 7px;
      }
    }
  }

  .page {
    flex: 1;
    width: 100%;
    position: relative;

    &:before {
      z-index: -1;
      content: "";
      @include absolute(0, 0, 0, 0);
      @include bg-img("../assets/img/bg.png");
      background-size: cover;
    }

    .left_top {
      @include size(459px, 388px);
      @include bg-img("../assets/img/left-top.png");
      position: absolute;
      z-index: -1;
      top: -90px;
      left: 0;
    }

    .right_bottom {
      @include size(472px, 444px);
      @include bg-img("../assets/img/right-bottom.png");
      position: absolute;
      z-index: -1;
      right: 0;
      bottom: 0;
    }
  }

  .footer {
    .powered {
      @include size(192px, 30px);
      position: absolute;
      left: 26px;
      bottom: 26px;
    }
  }
}
